import { Component, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from '../app/home/home.component';
import {StudentLoginComponent} from '../app/student-login/student-login.component';
import {TeacherLoginComponent} from '../app/teacher-login/teacher-login.component';
import { AdminDashboardComponent } from './admin-dashboard/admin-dashboard.component';
import { LearnerDetailsComponent } from './learner-details/learner-details.component';
import { TeacherDetailsComponent } from './teacher-details/teacher-details.component';
import { TeacherApprovalComponent } from './teacher-approval/teacher-approval.component';
import { UploadVideoComponent } from './upload-video/upload-video.component';
import { TeacherDashboardComponent } from './teacher-dashboard/teacher-dashboard.component';
import {TeacherUploadsComponent} from './teacher-uploads/teacher-uploads.component';
import {StudentSubscribeListComponent} from './student-subscribe-list/student-subscribe-list.component';
import {TeacherProfileComponent} from './teacher-profile/teacher-profile.component';
import {ShowVideoComponent} from './show-video/show-video.component';
import {LiveClassesComponent} from './live-classes/live-classes.component';
import {StudentPanelComponent} from './student-panel/student-panel.component';
import { StudentDashboardComponent } from './student-dashboard/student-dashboard.component';
import { StudentProfileComponent } from './student-profile/student-profile.component';
import { CourseComponent } from './course/course.component';
import { RunningClassComponent } from './running-class/running-class.component';
import { CompleteCourseComponent } from './complete-course/complete-course.component';
import { SavecoursesComponent } from './savecourses/savecourses.component';
import { StudentLiveClassComponent } from './student-live-class/student-live-class.component';
import { HelpComponent } from './help/help.component';
import { CourseDocumentComponent } from './course-document/course-document.component';
import { MainComponent } from './main/main.component';




const routes: Routes = [

  {path:'',component:HomeComponent},
  {path:'Studentlogin',component:StudentLoginComponent},
  {path:'Teacherlogin',component:TeacherLoginComponent},
  {path:'admin',component:AdminDashboardComponent},
  {path:'learner-list',component:LearnerDetailsComponent},
  {path:'teacher-list',component:TeacherDetailsComponent},
  {path:'approveTeacher/:id',component:TeacherApprovalComponent},
  {path:'uploadVideo',component:UploadVideoComponent},
  {path:'teacherDashboard',component:TeacherDashboardComponent}, 
  {path:'student-dashboard',component:StudentDashboardComponent, children:[
    {path:'', redirectTo:'Profile',pathMatch:'full'},
    {path:'Profile',component:StudentProfileComponent},
    {path:'course',component: CourseComponent,children:[
      {path:'', redirectTo:'Running',pathMatch:'full'},
      {path:'Running',component:RunningClassComponent},
      {path:'Complete',component: CompleteCourseComponent},
      {path:'save-courses',component:SavecoursesComponent},
      {path:'show-live',component:StudentLiveClassComponent}
    ]},
    {path:'help',component:HelpComponent},
    {path:'course-document',component: CourseDocumentComponent},
  ]}, 
  {path:'learner-list',component:LearnerDetailsComponent},
  {path:'teacher-list',component:TeacherDetailsComponent},
  {path:'runningcourse',component:RunningClassComponent},
  {path:'main-pannel',component:MainComponent}, 
  {path:'Profile',component:TeacherProfileComponent},
  {path:'liveClass',component:LiveClassesComponent},
  {path:'showVideo',component:ShowVideoComponent},
  {path:'studentSubscribeList',component:StudentSubscribeListComponent},
  {path:'uploadVideo',component:UploadVideoComponent},
  {path:'studentPanel',component:StudentPanelComponent}
  
  

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
