import { Component, OnInit } from '@angular/core';
import csc from 'country-state-city';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {HttpClient,HttpHeaders} from '@angular/common/http';
import {CookieService} from 'ngx-cookie-service'


@Component({
  selector: 'app-student-login',
  templateUrl: './student-login.component.html',
  styleUrls: ['./student-login.component.scss']
})
export class StudentLoginComponent implements OnInit {

  States:any
  Cities:any
  model:any={}
  loginDetail:any={}
  alert:string
  result={}
  
  
  constructor(private http:HttpClient,private route:Router,private cookieService:CookieService) {
    this.route.routeReuseStrategy.shouldReuseRoute = () => false;
      this.States=csc.getStatesOfCountry("IN");
      console.log(this.States)
     
  }

  onSubmit=(buttonType)=>{
    if(buttonType == 'SignUp')
    {
    console.log(this.model)
    const data=JSON.stringify(this.model);
    console.log(data)
    this.http.post('/api/Register',data,{headers:new HttpHeaders({'Content-Type':'application/json','Access-Control-Allow-Origin':'true','Access-Control-Allow-Headers':'true'})}).subscribe((res)=>{
  
    var value=res
    if(value["AlreadyRegistered"])
    {
      this.alert="Already Registered!! Please Login"
    }
    else{
      alert("Registered Successfully")

    }
  })
}
if(buttonType=='SignIn')
{
  const data=JSON.stringify(this.loginDetail)
  this.http.post('/api/studentLogin',data,{headers:new HttpHeaders({'Content-Type':'application/json','Access-Control-Allow-Origin':'true','Access-Control-Allow-Headers':'true'})}).subscribe((res)=>{
    console.log(res)
    this.result=res
    if(this.result["LoggedIn"])
    {
      const dateNow=new Date();
      console.log(dateNow.getHours())
      dateNow.setDate(dateNow.getDate()+1)
      console.log(dateNow)
      this.alert=""
      this.cookieService.set('Id',JSON.stringify(this.result["data"]), dateNow,'/')
      var items=this.cookieService.get('Id')
      setTimeout(()=>{this.route.navigate(['/main-pannel'])},1000)
    }
  })
}
    
  }


  getCities=(id)=>{
    this.Cities=csc.getCitiesOfState('IN',id)
  }


  ngOnInit(): void {

    const cookieExists:boolean= this.cookieService.check('Id')
    if(cookieExists)
    {
      this.route.navigate(['/main-pannel'])
    }


    var active= document.querySelector('#pills-home-tab');
    var active1= document.querySelector('#pills-profile-tab');
    var signin=document.querySelector('#pills-signin');
    var signup=document.querySelector('#pills-profile');

    active.addEventListener('click',function(){
      active.classList.add('act');
      signin.classList.add('show');
      signin.classList.add('active');
      signup.classList.remove('show');
      signup.classList.remove('active');
      active1.classList.remove('act');
    });

    active1.addEventListener('click',function(){
      active1.classList.add('act');
      signin.classList.remove('show');
      signin.classList.remove('active');
      signup.classList.add('show');
      signup.classList.add('active');
      active.classList.remove('act');
    });

  }

}
