import { Component, OnInit } from '@angular/core';
import {CookieService} from 'ngx-cookie-service'
import {HttpClient,HttpHeaders} from '@angular/common/http'
@Component({
  selector: 'app-show-video',
  templateUrl: './show-video.component.html',
  styleUrls: ['./show-video.component.scss']
})
export class ShowVideoComponent implements OnInit {

  public VideoForm:{
    
    Class:string,
    Subject:string
  }
  Files={}
  VideoFiles=[]
  
  constructor(private cookieService:CookieService,public http: HttpClient) { 
    this.VideoForm={   
      Class:"",
      Subject:""
    }
  }
  Teacher_Id=""
  regex=new RegExp('[^.]+$');
  showVideos(){
    this.VideoFiles=[]
    this.VideoForm.Subject=this.VideoForm.Subject.charAt(0).toUpperCase() + this.VideoForm.Subject.slice(1);
    this.VideoForm["Teacher_Id"]=this.Teacher_Id
    console.log(this.VideoForm)
    this.http.post('/api/showVideos',JSON.stringify(this.VideoForm),{headers:new HttpHeaders({'Content-Type':'application/json','Access-Control-Allow-Origin':'true','Access-Control-Allow-Headers':'true'})}).subscribe((res)=>{
      console.log(res)
      this.Files=res
      
      var count=0
      for(var i in this.Files)
      {
        if(this.Files[i].name.match(this.regex)=='mp4')

        {
        this.VideoFiles[count]=this.Files[i].name
        count++
        }
        
        console.log(this.VideoFiles)
        
      }

      

    })
  }




  ngOnInit(): void {

    
  
    this.Teacher_Id=JSON.parse(this.cookieService.get('User'))[0].Teacher_Id


  }

}
